.layout {
  width: 100%;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  color: #0c0c0c;
}

.layout__header {
  height: 80px;
  background: white;
  border-bottom: 1px solid #dedede;
}

.layout__body {
  width: 100%;
  height: 100%;
  padding: 2rem 4% 3rem;
  margin-top: 32px;
}

.layout__footer {
  height: 120px;
  background-color: #0c0c0c;
  margin-top: auto;
}

.layout__body h1 {
  font-size: 2rem;
}

.layout__body h2 {
  font-size: 1.5rem;
}

.layout__body h3 {
  font-size: 1.25rem;
}

@media screen and (max-width: 600px) {
  .layout__header {
    height: 90px;
  }
}
